import React from "react"
import PropTypes from "prop-types"
import "./SummaryTab.scss"

const SummaryTab = ({ isActive, carData, startDate }) => {
  const normalizeCarNumber = (number) => {
    const returnNumber = number.split("")
    returnNumber.splice(1, 0, " ")
    returnNumber.splice(5, 0, " ")
    returnNumber.splice(8, 0, " ")
    return returnNumber.join("")
  }

  if (!isActive) {
    return null
  }
  return (
    <div className="summary-tab">
      <div className="summary-tab__content">
        <h3 className="summary-tab__car-name">{carData.name}</h3>
        <div className="summary-tab__number car-license-card">
          {normalizeCarNumber(carData.number)}
        </div>
        <div className="summary-tab__tank">
          <b>Топливо</b> {carData.tank}%
        </div>
        <div className="summary-tab__start">
          <b>Доступна с</b> {startDate}
        </div>
      </div>
    </div>
  )
}
SummaryTab.propTypes = {
  isActive: PropTypes.bool,
  carData: PropTypes.objectOf(),
  startDate: PropTypes.string
}

export default SummaryTab
